header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 150px 50px;
}

.name {
  font-weight: bold;
  color: #111;
}

.headText {
  h1 {
    color: #555;
  }
}

.btnSendEmail {
  color: white;
  font-size: 1.25rem;
  background-color:#005580;
  width: 250px;
  height: 65px;
  border: none;
  border-radius: 50px;
  transition: linear 300ms;
}

.btnSendEmail:hover {
  background-color: #b3cde0;
  transition: linear 300ms;
  cursor: pointer;
}

.icon {
  color: #B4D8E7;
}

.icon:hover {
  color: #56BAEC;
}

.iconHover {
  transition: linear 200ms;
}

.socialNetworks {
  display: flex;
  width: 20%;
  height: 80px;
  justify-content: space-between;
  align-items: center;
  min-width: 250px;
}

@media only screen and (min-width: 992px) {
  header {
    padding: 150px 100px;
  }
}